<!-- chat -->
<template>
  <v-tab-item key="chat">
    <div
      v-if="会话模式.名称 === '金小税'"
      :class="['d-flex flex-row align-center my-2']"
    >
      <v-avatar
        size="64px"
      >
        <v-icon
          size="50"
          color="#0077F1"
        >
          mdi-robot-happy-outline
        </v-icon>
      </v-avatar>
      <span
        class="answer"
      >
        您好，很高兴为您服务，请问有什么可以帮助您？
      </span>
    </div>
    <div
      v-if="会话模式.名称 === '金小税'"
      class="golden-tax-faqs"
    >
      <p class="mdi-question">
        <span
          class="mdi mdi-chat-question"
          style="color:#007aff; margin-right:16px;font-size:28px;"
        /><span style="line-height: 32px;vertical-align: 6px;">猜你想问</span>
      </p>
      <ul>
        <li
          v-for="(faq,idx) in window.app.golden_tax_faqs"
          :key="idx"
          @click="askFaq(faq)"
        >
          {{ faq }}
        </li>
      </ul>
    </div>
    <div
      v-for="(current_conversation, index) in chatList"
      :key="index"
      :class="['d-flex flex-row align-center my-2', current_conversation.role == 'user' ? 'justify-end': null]"
    >
      <span
        v-if="current_conversation.role === 'user'"
        class="ask"
        autofocus
        v-text="current_conversation.content"
      />
      <v-hover v-slot="{ hover }">
        <div>
          <v-avatar
            :class="current_conversation.role === 'user' ? 'user-avatar': 'bot-avatar'"
            size="80"
            class="头像"
          >
            <!-- <span>{{ {user:"问",AI:'答'}[current_conversation.role] }}</span> -->
            <v-icon
              v-if="current_conversation.role === 'user'"
              size="50"
              color="#0077F1"
            >
              mdi-face-agent
            </v-icon>
            <v-icon
              v-else
              size="50"
              color="#0077F1"
            >
              mdi-robot-happy-outline
            </v-icon>
          </v-avatar>

          <br>
          <v-expand-transition>
            <div
              v-if="hover"
              class="float"
            >
              <v-icon
                v-if="!isLoading"
                size="32px"
                text
                @click="window.delete_current_conversation(current_conversation)"
              >
                mdi-delete
              </v-icon>

              <v-icon
                size="32px"
                text
                @click="window.copy(current_conversation.content)"
              >
                mdi-content-copy
              </v-icon>
            </div>
          </v-expand-transition>
        </div>
      </v-hover>

      <span
        v-if="current_conversation.role !== 'user'"
        class="answer"
        v-html="$util.md2html(current_conversation.content)"
      />
    </div>
    <div v-if="!isLoading&&chatList.length>0">
      <v-tooltip left>
        <template v-slot:activator="{ on, attrs }">
          <v-icon
            class="float"
            large
            text
            style="right: 0;"
            v-bind="attrs"
            v-on="on"
            @click="window.re_generate"
          >
            mdi-refresh
          </v-icon>
        </template>
        <span>{{ $t("re_generate") }}</span>
      </v-tooltip>
    </div>
    <br><br><br><br><br><br><br><br><br>
    <v-footer fixed>
      <div
        v-if="语音输入"
        style="position: fixed;bottom: 120px;width: 100%;z-index: 20;"
      >
        <v-btn
          v-if="!语音输入中"
          style="margin: auto;display: flex;"
          color="#0077F1"
          fab
          width="100"
          height="100"
          dark
          @click="()=>{ switchSiriWave(true); window.listen() }"
        >
          <v-icon size="54">
            mdi-microphone
          </v-icon>
        </v-btn>
        <v-btn
          v-if="语音输入中"
          style="margin: auto;display:flex;z-index:999;background:transparent;"
          :color="isLoading ? 'rgb(0, 78, 162, 0.3)':'transparent'"
          width="100"
          height="100"
          depressed
          plain
          icon
          light
          :loading="isLoading"
          @click="()=>{ switchSiriWave(false); window.stop_listen() }"
        >
          <v-icon
            color="red"
            size="54"
          >
            mdi-microphone-off
          </v-icon>
        </v-btn>
        <div
          v-show="语音输入中 && !isLoading"
          id="siri-container"
        />
      </div>
      <v-textarea
        v-model="问题"
        autofocus
        hide-details
        no-resize
        rows="3"
        :loading="isLoading"
        :placeholder="$t(会话模式.描述)"
        @keypress.enter="submitQuestion"
      />
      <div>
        <v-btn
          v-if="isLoading"
          style="margin-bottom: 10px;margin-top: 0;"
          color="#004EA2"
          rounded
          dark
          size="x-large"
          @click="window.中断()"
        >
          {{ $t("abord") }}
        </v-btn>
        <v-btn
          v-if="!isLoading"
          style="margin-bottom: 10px;margin-top: 0; border-radius: 26px;"
          dark
          color="#004EA2"
          rounded
          large
          @click="submitQuestion"
        >
          {{ $t("send") }}
        </v-btn>
        <br>
        <v-expand-transition>
          <v-chip
            v-if="会话模式.名称!=''"
            color="#004EA2"
            outlined
            close
            @click:close="$emit('reset-mode')"
          >
            {{ 会话模式.名称 }}
          </v-chip>
        </v-expand-transition>
      </div>
    </v-footer>
  </v-tab-item>
</template>

<script>
import SiriWave from 'siriwave'
export default {
  name: 'TabItemChat',
  props: {
    chats: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    },
    question: {
      type: String,
      default: ''
    },
    voiceInputing: {
      type: Boolean,
      default: false
    },
    voiceEnable: {
      type: Boolean,
      default: false
    },
    conversitionMode: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      window: window,
      chatList: [],
      问题: '',
      会话模式: {
        名称: '',
        描述: 'input_question',
        问题: ''
      },
      waveIndicator: null
    }
  },
  computed: {
    isLoading: {
      get() {
        return this.loading
      },
      set(v) {}
    },
    语音输入: {
      get() {
        return this.voiceEnable
      },
      set(v) {}
    },
    语音输入中: {
      get() {
        return this.voiceInputing
      },
      set(v) {}
    }
  },
  watch: {
    '$props.conversitionMode': {
      handler(v) {
        this.会话模式 = { ...v }
      },
      deep: true
    },
    '$props.question': {
      handler(v) {
        this.问题 = v
      }
    },
    '$props.chats': {
      handler(v) {
        this.chatList = [...v]
      },
      deep: true
    }
  },
  created() {
    this.chatList = this.chats
    this.问题 = this.question
    this.语音输入 = this.voiceEnable
    this.语音输入中 = this.voiceInputing
    this.会话模式 = { ...this.conversitionMode }
  },
  methods: {
    askFaq(v) {
      if (this.isLoading) {
        return
      }
      this.$emit('question-changed', v)
    },
    submitQuestion() {
      if (this.isLoading) {
        return
      }
      this.$emit('question-changed', this.问题)
    },
    switchSiriWave(isShow) {
      if (isShow) {
        if (!this.waveIndicator) {
          this.waveIndicator = new SiriWave({
            container: document.querySelector('#siri-container'),
            color: '#4caf50',
            width: 200,
            height: 30,
            speed: 0.1,
            amplitude: 1
          })
        } else {
          this.waveIndicator.start()
        }
      } else {
        this.waveIndicator.stop()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.golden-tax-faqs{
  padding: 24px;
  margin-left: 80px;
  width: 36%;
  border: 1px solid rgba(0,119,241,0.30);
  box-shadow: 0 0 4px 0 rgba(188,197,212,.4);
  border-radius: 8px;
  ul{
    padding:0;
    margin:0;
    width:100%;
    li{
      margin-left: 24px;
      line-height: 36px;
      color:#1768f3;
      cursor:pointer;
      border-bottom: 1px solid #edf0f6;
    }
  }
}

.v-footer.v-sheet.theme--light.v-footer--fixed{
  background: #F2F7FB;
  border-radius: 16px;
}
#siri-container{
    position: absolute;
    top: 48px;
    left: calc(50% - 100px);
    width: 200;
    background: transparent;
    margin: auto;
    display: flex;
}

</style>

