<!-- 顶栏工具条 -->
<template>
  <v-toolbar
    float
    dark
    height="60"
  >
    <v-app-bar-nav-icon @click="$emit('show-menu')" />
    <img
      style="width:40px;height:24px;margin:14px;"
      src="data:image/png;base64,"
    >
    <v-toolbar-title>
      <a
        style="text-decoration: none;color: white;"
        href="#"
      >{{ $t("wenda") }}</a>
    </v-toolbar-title>
    <v-spacer />
    <span
      :title="$t('zhishiku')"
      :class="{'top-button': true, 'is-active': zhishiku_on}"
      class="mdi mdi-book-open-page-variant-outline"
      @click="toggleZhishikuOn"
    />
    <span
      :title="$t('history')"
      :class="{'top-button': true, 'is-active': history_on}"
      class="mdi mdi-history"
      @click="toggleHistoryOn"
    />
    <!-- <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">

      </template>
      <span>{{ $t("zhishiku") }}</span>
    </v-tooltip>
				&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">

      </template> -->
    <!-- <span>{{ $t("history") }}</span>
    </v-tooltip> -->
				&nbsp;&nbsp;&nbsp;&nbsp;
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <div
          v-bind="attrs"
          v-on="on"
        >
          <v-icon
            text
            @click="onClearHistory"
          >
            mdi-delete-clock-outline
          </v-icon>
        </div>
      </template>
      <span>{{ $t("clear_history") }}</span>
    </v-tooltip>
  </v-toolbar>
</template>

<script>
export default {
  name: 'TopBar',
  components: {
  },
  props: {
    historyOn: {
      type: Boolean,
      default: false
    },
    zhishikuOn: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      history_on: false,
      zhishiku_on: false
    }
  },
  watch: {
    '$props.historyOn': {
      handler(v) {
        this.history_on = v
      }
    },
    '$props.zhishikuOn': {
      handler(v) {
        this.zhishiku_on = v
      }
    }
  },
  created() {
    this.history_on = this.historyOn
    this.zhishiku_on = this.zhishikuOn
  },
  methods: {
    onClearHistory() {
      this.$emit('clear-history')
    },
    toggleHistoryOn() {
      this.$emit('toggle-history-on')
    },
    toggleZhishikuOn() {
      this.$emit('toggle-zhishiku-on')
    }
  }
}
</script>

<style lang='scss'>
.v-toolbar.theme--dark.v-sheet{
  background: #004EA2!important;
  box-shadow: 0 3px 4px 0 rgba(0,78,162,0.20) !important;
  height: 60px;
  .v-toolbar__title{
    font-family: SourceHanSansCN-Medium;
    font-size: 21px;
    color: #FFFFFF;
    letter-spacing: 0;
    font-weight: 500;
  }
  .mdi.top-button{
    font-size:24px;
    margin:8px 16px;
    cursor:pointer;
    color: white;
    width: 36px;
    height: 36px;
    display: inline-block;
    border-radius: 18px;
    text-align: center;
    &.is-active{
      color:#000;
      background: #fff;
    }
  }
}
</style>
