export default {
  data() {
    return {
      _golden_tax_url: '',
      window: window,
      // variables bellow are synced  from wenda index.html
      问题: '',
      chat: JSON.parse(localStorage['wenda_chat_history'] || '[]'),
      history_on: false,
      history_limit: 5,
      功能菜单: [],
      会话模式: {
        名称: '',
        描述: 'input_question',
        问题: ''
      },
      plugins: [],
      simplify_historical_information: true,
      语音输入: false,
      语音播报: false,
      语音输入中: false,
      test_zsk_prompt: '',
      test_zsk_step: 1,
      zhishiku: [],
      zhishiku_dynamic: [],
      setting: {},
      tab: 0,
      zhishiku_on: false,
      temperature: 0.8,
      max_length: 2048,
      top_p: 0.8,
      llm_type: '',
      tabs: ['chat', 'zhishiku', 'setting'],
      snackbar: false,
      snackbar_text: '',
      loading: false,
      drawer: false,
      synthesisUtterance: null
    }
  },
  created() {
    // 组件实例暴露到windows
    window.app = this
  },
  beforeDestroy() {
    window.app = null
    if (speechSynthesis) {
      console.log('speechSynthesis', speechSynthesis)
      console.log('speechSynthesis voices', speechSynthesis.getVoices())
      // speechSynthesis.stop()
    }
  },
  mounted() {
    const { _golden_tax_url, _autos_js_loaded } = this.$util.getAppSettings()
    this._golden_tax_url = _golden_tax_url
    this.dynamicLoadScript('/api/llm').then(() => {
      this.loadAutoJSFuctions(_autos_js_loaded).then(() => {
        this.功能菜单 = window.功能
      })
    })
  },
  methods: {
    load_feature(func) {
      this.会话模式 = func
      this.drawer = false
    },
    clear_history() {
      this.chat = []
      this.save_history()
      this.cancel_speaking()
    },
    cancel_speaking() {
      if (speechSynthesis.speaking) {
        if (typeof speechSynthesis.cancel === 'function') {
          speechSynthesis.cancel()
        } else {
          speechSynthesis.stop()
        }
      }
    },
    /**
     *  播放合成语音 SynthesisUtterance
     *
     * @param {*} s
     * @param {*} config pitch[0-2] volume[0-1]
     */
    speak(s, config) {
      // this.cancel_speaking()
      if (!this.synthesisUtterance) {
        this.synthesisUtterance = new SpeechSynthesisUtterance()
      }
      this.synthesisUtterance.rate = 1
      this.synthesisUtterance.pitch = 10
      this.synthesisUtterance.text = s
      this.synthesisUtterance.volume = 1
      if (config) {
        Object.assign(this.synthesisUtterance, config)
      }
      speechSynthesis.speak(this.synthesisUtterance)
    },
    onQuestionChanged(question) {
      this.问题 = question
      window.提交()
    },
    save_history() {
      localStorage['wenda_chat_history'] = JSON.stringify(this.chat)
    },
    resetConversitionMode() {
      this.会话模式 = { 名称: '', 描述: 'input_question', 问题: '' }
    },
    alert(text) {
      this.snackbar_text = text// .replace(/\n/g,"<br>")
      this.snackbar = true
    },
    dynamicLoadScript(src) {
      return new Promise((resolve, reject) => {
        const autojs = document.createElement('script')
        autojs.src = src
        autojs.type = 'text/javascript'
        autojs.onload = function() {
          resolve()
        }
        autojs.onerror = function(err) {
          console.error(err)
          reject(new Error('Failed to load ' + src))
        }
        document.body.appendChild(autojs)
      })
    },
    loadAutoJSFuctions(auto_menus) {
      return Promise.all(auto_menus.map(src => {
        return this.dynamicLoadScript(`/autos/${src}.js`)
      }))
    },
    scrollToBottom() {
      this.$nextTick(() => {
        window.scrollTo(0, document.body.scrollHeight)
      })
    },
    /**
     * 打字机效果
     *
     * @param {*} talk 对话对象 格式： { role: "AI", content: '……' }
     * @param {*} prop 属性标题
     * @param {*} resData 返回数据
     * @param {*} speak 语音播报
     */
    loopPrintWords(talk, prop, resData, speak) {
      talk.content = `#### ${prop}: \n`
      this.chat.push(talk)
      const contentWords = resData
      if (speak) {
        const trimedContent = (talk.content + contentWords).replace(/[#\s]*/, '')
        this.cancel_speaking()
        this.speak(trimedContent, { rate: 1, pitch: 1 })
      }

      let i = 0
      const timer = setInterval(() => {
        if (i < contentWords.length) {
          talk.content += contentWords[i]
          if (contentWords[i] === '\n') {
            this.scrollToBottom()
          }
          i++
        } else {
          clearInterval(timer)
        }
      }, 100)
    }
  }
}
