import markdownit from 'markdown-it'
export default {
  getAppSettings() {
    const { _golden_tax_url, _autos_js_loaded } = window
    return {
      _golden_tax_url,
      _autos_js_loaded
    }
  },
  copy_to_clipboard(s) {
    navigator.permissions.query({ name: 'clipboard-write' }).then(result => {
      if (result.state === 'granted' || result.state === 'prompt') {
        navigator.clipboard.writeText(s.replace(/\n+/g, '\n'))
          .then(() => {
            alert('文本已经成功复制到剪切板')
            console.log('文本已经成功复制到剪切板')
          })
          .catch(err => {
            console.log('copy clipboard error:', err)
          })
      } else {
        alert('当前无操作权限。请使用最新版本Chrome浏览器，并在浏览器高级设置-页面设置中允许访问剪切板')
        console.log('当前无操作权限。请使用最新版本Chrome浏览器，并在浏览器高级设置-页面设置中允许访问剪切板')
      }
    })
  },
  md2html(conent) {
    // return conent
    conent = String(conent)
    const md = new markdownit()
    // md.disable(['link', 'image'])
    return md.render(conent).replace(/<a /g, '<a target="_blank"')
  },
  find_RomanNumerals(str) {
    const RomanNumeralsMap = {
      'III': 3,
      'II': 2,
      'IV': 4,
      'IX': 9,
      'XL': 40,
      'XC': 90,
      'CD': 400,
      'CM': 900,
      'I': 1,
      'V': 5,
      'X': 10,
      'L': 50,
      'C': 100,
      'D': 500,
      'M': 1000
    }
    let number = 0
    for (var p in RomanNumeralsMap) {
      if (str.indexOf(p) !== -1) {
        str = str.split(p).join('')
        number += RomanNumeralsMap[p]
      }
    }
    return number
  }
}
