import Vue from 'vue'
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

export default new VueI18n({
  locale: navigator.language,
  messages: {
    'zh-CN': require('@/assets/languages/zh.json'),
    'en': require('@/assets/languages/en.json'),
    'ja': require('@/assets/languages/ja.json')
  }
})
